import React, { useState, useEffect, Fragment } from 'react';
import useStyles from './styles';
import Button from '../components/button';
import Input from '../components/input';
import { updateObject, checkValidity } from '../shared/utility';
import { Checkbox, Box } from "@material-ui/core";
import Spinner from '../components/loader';
import { Api, updateJWTToken } from '../shared/helper';
import { Connect, mapDispatchToProps, mapStateToProps } from '../redux';
import Loader from '../components/loader';
import queryString from 'query-string'
function Login(props) {
    const [state, setState] = useState({
        loading: false,
        error: null,
        inputs: {
            username: {
                elementType: 'input',
                elementConfig: {
                    type: 'name',
                    label: 'Username/Email'
                },
                value: '',
                error: '',
                validation: {
                    required: true
                },
                valid: false,
                touched: false
            },
            password: {
                elementType: 'input',
                elementConfig: {
                    type: 'password',
                    label: "Pin"
                },
                value: '',
                error: '',
                validation: {
                    required: true
                },
                valid: false,
                touched: false
            }
        },
        user: null,
        loggedIn: false,
        loading: false,
        loginError: null,
        transaction: null,
        callback: null
    }),
        { inputs, error, transaction, loading, callback, app, loginError } = state,
        classes = useStyles(),
        { history } = props,
        params = queryString.parse(props?.location?.search),
        inputChangedHandler = (event, inputName) => {
            const updatedInputs = updateObject(inputs, {
                [inputName]: updateObject(inputs[inputName], {
                    value: event.target.value,
                    valid: checkValidity(event.target.value, inputs[inputName].validation).isValid,
                    error: checkValidity(event.target.value, inputs[inputName].validation).error,
                    touched: true
                })
            });
            setState({ ...state, inputs: updatedInputs });
        },
        decrypt = () => {
            setState({ ...state, loading: true })
            Api('/decrypt_app_payment',
                {
                    encrypted: true,
                    data: params.data
                }).then((resp) => {
                    if (resp?.error)
                        setState({ ...state, loading: false, error: resp.error });
                    else {
                        updateJWTToken(resp?.access_token);
                        if (props.history.location.pathname === '/login')
                            setState({ ...state, app: resp.app, loading: false, callback: resp.callback })
                        else {
                            let Transaction = {
                                checkOutInfo: resp.transaction,
                                app: resp.app,
                                recepient: resp.recepient
                            };
                            setState({
                                ...state,
                                loading: false,
                                transaction: Transaction
                            });
                        }
                    }
                }).catch(err => {
                    setState({ ...state, loading: false, error: "An error occured please try again later." })
                })
        },
        submitHandler = () => {
            if (inputs.username.valid && inputs.password.valid) {
                setState({ ...state, loading: true });
                Api('/', {
                    module: "Apps",
                    data: {
                        action: "LoginUser",
                        Email: inputs.username.value,
                        Pin: inputs.password.value,
                        userAction: props.history.location.pathname === '/login' ? 'Login' : 'Payment',
                        userId: 'app',
                        socketId: params.socketId,
                        appId: app?._id,
                        BenkikoSdk: true
                    }
                }).then(({ error, user }) => {
                    if (error)
                        setState({ ...state, loginError: error.Email, loading: false });
                    else if (props.history.location.pathname === '/login')
                        window.close()

                    else
                        history.push({ pathname: '/pay', user: user, transaction, socketId: params.socketId });

                }).catch(err => {
                    setState({ ...state, loading: false });
                })
            } else
                setState({ ...state, loginError: 'Enter valid details' })
        }
    let form = Object.keys(inputs).map(input => (
        <Input
            key={input}
            elementType={inputs[input].elementType}
            elementConfig={inputs[input].elementConfig}
            value={inputs[input].value}
            error={inputs[input].error}
            invalid={!inputs[input].valid}
            shouldValidate={inputs[input].validation}
            touched={inputs[input].touched}
            changed={(event) => inputChangedHandler(event, input)}
        />
    ))
    useEffect(() => {
        decrypt()
        return () => {

        }
    }, [])
    return (
        <Fragment>
            {loading ? <div className={classes.load}><Loader /></div> : error ? <div className={classes.main}>
                <Box className={classes.payBg} boxShadow={3}>
                    <div className={classes.paymentHeader}><div>{props.history.location.pathname === '/login' ? 'Login' : 'Pay'} with Benkiko</div> <img className={classes.payImg} src="https://benkiko.s3.us-east-2.amazonaws.com/logo.png" /> </div>
                    <div className={classes.appError}>{error}</div>
                </Box>
            </div> :
                <div className={classes.main}>
                    <Box className={classes.bg} boxShadow={3}>
                        <div className={classes.loginHeader}>
                            <img
                                className={classes.img}
                                src="https://benkiko.s3.us-east-2.amazonaws.com/logo.png"
                                alt="Benkiko"
                            />
                            <div className={classes.headerTitle}>Benkiko</div>
                        </div>
                        <form>
                            {form}
                            {loginError ? <div className={classes.error}>{loginError}</div> : null}
                            <div className={classes.loginFooter}>
                                {/* <Checkbox color="default" onChange={handleChange} /> <div>Remember Me</div> */}
                            </div>
                            <div className={classes.btnWrapper}>
                                {loading ? <Spinner /> :
                                    <Button onClick={() => submitHandler()}>Login</Button>}
                            </div>
                        </form>
                    </Box>
                </div>
            }
        </Fragment>
    )
}

export default Connect(mapStateToProps, mapDispatchToProps)(Login)
